          <span :class="[
                positiveNegative(
                  company.full_data.analysis.data.extended.data.analysis.value
                    .return_5yr_abs
                ),
                'share-return'
              ]">{{
                (
                  company.full_data.analysis.data.extended.data.analysis.value
                    .return_5yr_abs * 100
                ).toFixed(2)
