        <li class="share-return-li">
          P/B:
          <span :class="[
                pbookValueColor(company.pb),
                'share-return',
                'cv-grid-story__preview-col',
                'add-margin'
              ]">{{
                (company.pb &&
                  company.pb.toFixed(2)) ||
                  "-"
              }}</span>
        </li>
      </cv-column>
      <cv-column>
        <li class="share-return-li">
          ROE:
          <span :class="[
                roeValueColor(company.roe),
                'share-return',
                'cv-grid-story__preview-col',
              ]">{{
                company.roe ?
                  company.roe.toFixed(2) + '%' :
                  "-"
              }}</span>
        </li>
        <li class="share-return-li">
          ROA:
          <span :class="[
                roaValueColor(company.roa),
                'share-return',
                'cv-grid-story__preview-col'
              ]">{{
                company.roa ?
                  company.roa.toFixed(2) + '%' :
                  "-"
              }}</span>
        </li>
        <li class="share-return-li">
          EPS:
          <span :class="[
                'share-return',
                'cv-grid-story__preview-col'
              ]">{{
                company.eps ?
                  company.eps.toFixed(2) :
                  "-"
              }}</span>
        </li>
      </cv-column>
      <cv-column>
        <li class="share-return-li">
          Debt to Equity:
          <span :class="[
                'share-return',
                'cv-grid-story__preview-col'
              ]">{{
                company.debt_equity ?
                  (company.debt_equity * 100).toFixed(2) + '%' :
                  "-"
              }}</span>
        </li>
      </cv-column>
      <cv-column>
        <apexchart class="shares-prices" type="area" :options="chartOptionsEarnings" :series="seriesEarnings[company.full_data.company_id]"></apexchart>
      </cv-column>
      <cv-column>
        <li class="share-return-li">
          Current Yield:
          <span :class="[
                dividendYield(
                  company.dividend_yield
                ),
                'share-return'
              ]">{{
                (
                  company.dividend_yield * 100
                ).toFixed(2)
              }}%</span>
        </li>
        <li class="share-return-li">
          Future Yield:
          <span :class="[
                dividendYield(
                  company.full_data.analysis.data.extended.data.analysis.dividend
                    .dividend_yield_future
                ),
                'share-return'
              ]">{{
                (
                  company.full_data.analysis.data.extended.data.analysis.dividend
                    .dividend_yield_future * 100
                ).toFixed(2)
              }}%</span>
        </li>
      </cv-column>
