<script>
import _ from "lodash";
import axios from "axios";
import numeral from 'numeral';

import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/default.css'

export default {
  name: "Home",
  components: {
    VueSlider
  },
  data() {
    return {
      companies: [],
      currentCountry: this.$route.params.country,
      filter: {
        market_cap: ['0', ''],
        share_price: ['0', ''],
        value: ['0%', '50%'],
        pe: ['0', '20'],
        peg: ['0', '5'],
        pb: ['0', '10'],
        roe: ['25%', ''],
        roa: ['10%', ''],
        eps: ['0', '100'],
        debt_equity: ['0%', ''],
        dividend_yield: ['0%', ''],
        search_term: "",
        limit: this.$route.query.limit || 25,
      },
      sortable: false,
      columns: [{
          label: "Name",
          sortable: true
        },
        {
          label: "Value",
          sortable: true
        },
        {
          label: "Shares Price (Valutation)",
          sortable: true
        },
        {
          label: "Shares History",
          sortable: true
        },
        {
          label: "Shares Return",
          sortable: true
        },
        {
          label: "Indicators",
          sortable: true
        },
        {
          label: "Performance",
          sortable: true
        },
        {
          label: "Debt",
          sortable: true
        },
        {
          label: "Future Earnings per Share",
          sortable: true
        },
        {
          label: "Dividends",
          sortable: true
        }
      ],

      chartOptionsShares: {
        chart: {
          type: "area",
          stacked: false,
          height: 150,
          zoom: {
            enabled: false
          },
          toolbar: {
            show: false
          }
        },
        dataLabels: {
          enabled: false
        },
        markers: {
          size: 0
        },
        fill: {
          type: "gradient",
          gradient: {
            shadeIntensity: 1,
            inverseColors: false,
            opacityFrom: 0.5,
            opacityTo: 0,
            stops: [0, 90, 100]
          }
        },
        title: {
          text: undefined,
          align: "left",
          margin: 0
        },
        yaxis: {
          show: false
        },
        xaxis: {
          type: "datetime"
        },
        tooltip: {
          shared: false,
          x: {
            show: true,
            format: "dd MMM yyyy"
          },
          y: {
            formatter: function(val) {
              return val.toFixed(2);
            }
          }
        }
      },
      chartOptionsEarnings: {
        chart: {
          type: "area",
          stacked: false,
          height: 150,
          zoom: {
            enabled: false
          },
          toolbar: {
            show: false
          }
        },
        dataLabels: {
          enabled: false
        },
        markers: {
          size: 0
        },
        fill: {
          type: "gradient",
          gradient: {
            shadeIntensity: 1,
            inverseColors: false,
            opacityFrom: 0.5,
            opacityTo: 0,
            stops: [0, 90, 100]
          }
        },
        title: {
          text: undefined,
          align: "left",
          margin: 0
        },
        yaxis: {
          show: false
        },
        xaxis: {
          type: "datetime"
        },
        tooltip: {
          shared: false,
          x: {
            show: true,
            format: "dd MMM yyyy"
          },
          y: {
            formatter: function(val) {
              return val.toFixed(2);
            }
          }
        }
      },
      chartOptionsScore: {
        chart: {
          // height: 250,
          // width: 250,
          type: "radar",
          zoom: {
            enabled: false
          },
          toolbar: {
            show: false
          }
        },
        title: {
          text: undefined,
          align: "left",
          margin: 0
        },
        xaxis: {
          categories: ["Value", "Income", "Health", "Past", "Future"]
        },
        yaxis: {
          show: false
        }
      },
      seriesShares: {},
      seriesEarnings: {},
      seriesScore: {},
      isLoading: true,
      empty: false
    };
  },
  mounted() {
    this.getResults(this.currentCountry, false, false);
  },
  methods: {
    parseFilters(filter) {
      var low = filter[0];
      var high = filter[1];

      switch (low.slice(-1)) {
        case 't':
        case 'b':
        case 'm':
        case 'k':
          low = numeral(low).value() / 1000000;
          break;

        case '%':
          low = parseInt(low.substring(0, low.length - 1));
          break;
      }

      switch (high.slice(-1)) {
        case 't':
        case 'b':
        case 'm':
        case 'k':
          high = numeral(high).value() / 1000000;
          break;

        case '%':
          high = parseInt(high.substring(0, high.length - 1));
          break;
      }

      return low + "_" + high;
    },
    getResults(country, reload, filtered) {
      var context = this;
      this.isLoading = reload;

      // parse filters
      var filterURL = "";
      if (this.filter.search_term.length > 0) {
        filterURL = 'name=regex:' + this.filter.search_term;
      }
      if (filtered) {
        filterURL = [
          'market_cap=between:' + this.parseFilters(this.filter.market_cap),
          'share_price=between:' + this.parseFilters(this.filter.share_price),
          'value=between:' + this.parseFilters(this.filter.value),
          'pe=between:' + this.parseFilters(this.filter.pe),
          'peg=between:' + this.parseFilters(this.filter.peg),
          'pb=between:' + this.parseFilters(this.filter.pb),
          'roe=between:' + this.parseFilters(this.filter.roe),
          'roa=between:' + this.parseFilters(this.filter.roa),
          'eps=between:' + this.parseFilters(this.filter.eps),
          'debt_equity=between:' + this.parseFilters(this.filter.debt_equity),
          'dividend_yield=between:' + this.parseFilters(this.filter.dividend_yield),
        ].join('&');
      }

      axios
        .get('https://stocks-markets-api.herokuapp.com/stocks/' + this.currentCountry + '?limit=' + this.filter.limit + '&' + filterURL)
        .then(function(response) {
          var companies = response.data;

          context.empty = false;
          if (companies.length == 0) {
            context.isLoading = false;
            context.empty = true;
            return;
          }

          var seriesShares = {};
          var seriesEarnings = {};
          var seriesScore = {};
          for (var c in companies) {
            var company = companies[c];
            seriesShares[company.full_data.company_id] = [{
              name: "Stock Price",
              data: []
            }];

            seriesEarnings[company.full_data.company_id] = [{
                name: "Low",
                data: []
              },
              {
                name: "Mid",
                data: []
              },
              {
                name: "High",
                data: []
              }
            ];

            seriesScore[company.full_data.company_id] = [{
              name: "Score",
              data: []
            }];

            context.getSeriesShares(company.full_data.company_id);
            context.getSeriesEarnings(company.full_data);
            context.getSeriesScore(company.full_data);
          }

          context.companies = companies;
          context.seriesShares = seriesShares;
        })
    },
    changeCountry(country) {
      localStorage.setItem("country", country);
      this.isLoading = true;
      this.$router.push({
        path: "/" + country + '?limit=' + this.filter.limit
      });

      var context = this;
      setTimeout(function() {
        context.getResults(context.currentCountry, false, false);
      }, 250);
    },
    getDescendantProp(obj, desc) {
      var arr = desc.split(".");
      while (arr.length && (obj = obj[arr.shift()]));
      return obj;
    },
    sort(obj, criteria) {
      obj.sort((a, b) =>
        this.getDescendantProp(a, criteria) <
        this.getDescendantProp(b, criteria) ?
        1 :
        this.getDescendantProp(b, criteria) <
        this.getDescendantProp(a, criteria) ?
        -1 :
        0
      );

      return obj;
    },
    consecutive(arr) {
      // convert array to int
      arr = arr.map(num => new Date(parseInt(num)).getFullYear());

      // unique array
      arr = arr.filter(function(item, pos) {
        return arr.indexOf(item) == pos;
      });

      // reverse date
      arr = arr.reverse();

      // check if number are consecutive
      return arr.every((num, i) => (arr[i + 1] || num + 1) - num === 1);
    },
    valutationColor(value) {
      if (value >= 20) {
        return "success";
      }

      if (value < 20 && value > -20) {
        return "warning";
      }

      if (value <= -20) {
        return "error";
      }
    },
    dividendYieldColor(yieldValue) {
      if (yieldValue <= 0.01) {
        return "error-bg";
      }

      if (yieldValue > 0.01 && yieldValue < 0.04) {
        return "warning-bg";
      }

      if (yieldValue >= 0.04) {
        return "success-bg";
      }
    },
    peRatioColor(peRatio) {
      if (peRatio <= 10) {
        return "success";
      }

      if (peRatio > 10 && peRatio <= 40) {
        return "warning";
      }

      if (peRatio > 40) {
        return "error";
      }
    },
    pegRatioColor(pegRatio) {
      if (pegRatio <= 1) {
        return "success";
      }

      if (pegRatio > 1 && pegRatio <= 3) {
        return "warning";
      }

      if (pegRatio > 3) {
        return "error";
      }
    },
    pbookValueColor(pbookValue) {
      if (pbookValue <= 5) {
        return "success";
      }

      if (pbookValue > 5 && pbookValue <= 15) {
        return "warning";
      }

      if (pbookValue > 15) {
        return "error";
      }
    },
    roeValueColor(roeValue) {
      if (roeValue < 0) {
        return "error";
      }

      if (roeValue >= 0 && roeValue <= 10) {
        return "error";
      }

      if (roeValue > 10 && roeValue <= 30) {
        return "warning";
      }

      if (roeValue > 30) {
        return "success";
      }
    },
    roaValueColor(roaValue) {
      if (roaValue < 2) {
        return "error";
      }

      if (roaValue >= 2 && roaValue <= 8) {
        return "warning";
      }

      if (roaValue > 8) {
        return "success";
      }
    },
    roaValueColor(roaValue) {
      if (roaValue < 2) {
        return "error";
      }

      if (roaValue >= 2 && roaValue <= 8) {
        return "warning";
      }

      if (roaValue > 8) {
        return "success";
      }
    },
    positiveNegative(value) {
      if (value < 0) {
        return "error";
      }

      if (value == 0) {
        return "warning";
      }

      if (value > 0) {
        return "success";
      }
    },
    annualGrowth(growth) {
      if (growth < 0.1) {
        return "error";
      }

      if (growth >= 0.1 && growth <= 0.2) {
        return "warning";
      }

      if (growth > 0.2) {
        return "success";
      }
    },
    dividendYield(dividend) {
      if (dividend < 0.02) {
        return "error";
      }

      if (dividend >= 0.02 && dividend <= 0.06) {
        return "warning";
      }

      if (dividend > 0.06) {
        return "success";
      }
    },
    getSeriesShares(companyId) {
      var context = this;
      axios
        .get(
          `https://api.simplywall.st/api/company/price/${companyId}?start_timestamp=1467990900000`
        )
        .then(function(response) {
          var values = [];
          for (var r in response.data.data) {
            var res = response.data.data[r];
            values.push([res.date, res.close]);
          }

          context.isLoading = false;
          context.seriesShares[companyId] = [{
            name: "Stock Price",
            data: values
          }];
        });
    },
    getSeriesEarnings(company) {
      var context = this;

      var lowEarnings =
        company.analysis.data.extended.data.analysis.future
        .merged_future_earnings_per_share_low;
      var midEarnings =
        company.analysis.data.extended.data.analysis.future
        .merged_future_earnings_per_share;
      var highEarnings =
        company.analysis.data.extended.data.analysis.future
        .merged_future_earnings_per_share_high;

      var lEarn = [];
      var mEarn = [];
      var hEarn = [];
      for (var e in lowEarnings) {
        lEarn.push([parseInt(e), lowEarnings[e]]);
      }
      for (var e in midEarnings) {
        mEarn.push([parseInt(e), midEarnings[e]]);
      }
      for (var e in highEarnings) {
        hEarn.push([parseInt(e), highEarnings[e]]);
      }

      context.seriesEarnings[company.company_id] = [{
          name: "Low",
          data: lEarn
        },
        {
          name: "Mid",
          data: mEarn
        },
        {
          name: "High",
          data: hEarn
        }
      ];
    },
    getSeriesScore(company) {
      var context = this;

      var scores = company.analysis.data.extended.data.scores;
      context.seriesScore[company.company_id] = [{
        name: "Series 1",
        data: [
          scores.value,
          scores.income,
          scores.health,
          scores.past,
          scores.future
        ]
      }];
    }
  }
};
</script>

